import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';

export class Device {
  token: String;
  platform: String;
}

export class Account {
  uuid: String;
  username:String;
  email: String;
  notification_key: String;
  notification_key_name: String;
  auth_token: String;
  devices: Device[];
}


@Injectable()
export class DemoService {

  private baseUrl = '/api'; //'http://127.0.0.1:8000/api'

  //private acct:Account;
  

  constructor(private http:Http) { }

  retrieve(account_id): Promise<Account> {
    return this.http.get(this.baseUrl + '/accounts/' + account_id)
      .toPromise()
      .then(response => {
        return response.json() as Account
      })
      .catch(this.handleError);
  }

  list_accounts(): Promise<Account[]> {
    return this.http.get(this.baseUrl + '/accounts/')
      .toPromise()
      .then(response => {
        return response.json() as Account[]
      })
      .catch(this.handleError);
  }


  login(params): Promise<Account> {

    return this.http.post(this.baseUrl + '/accounts/login/', params)
      .toPromise()
      .then(response => {
        return response.json() as Account;
      })
      .catch(this.handleError);
  }

  register(params): Promise<Account> {
    return this.http.post(this.baseUrl + '/accounts/register/', params)
      .toPromise()
      .then(response => {
        return response.json() as Account;
      })
      .catch(this.handleError);
  }


  private handleError (error: any): Promise<any> {
    //console.error('Error >>> ', error);
    var errors = error.json();
    var errMsg = Object.keys(errors).map(k => (k == 'non_field_errors' ? '' : k.toUpperCase() + ': ') + errors[k]).join('\n');
    return Promise.reject(errMsg);
  }
}



