import { Injectable, Inject } from '@angular/core';
import { Http, Headers } from '@angular/http';

import { FirebaseApp } from "angularfire2";
import * as firebase from 'firebase';

import { BehaviorSubject  } from 'rxjs/BehaviorSubject';


export class Notification {
  title: string;
  body: string;
}

export class Payload {
  notification: string
}
export class MessageData {
  data: Payload
}


@Injectable()
export class FcmService {

  private baseUrl = '/api'; //'http://127.0.0.1:8000/api';
  private _messaging: firebase.messaging.Messaging;
  public currentToken: BehaviorSubject <string>;

  constructor(
    private http: Http,
    @Inject(FirebaseApp) private _firebaseApp: firebase.app.App
  ){

    this.currentToken = new BehaviorSubject<string>('');
    
    navigator.serviceWorker.register('./sw.js')
      .then(registration => {
        this._messaging = firebase.messaging(this._firebaseApp);
        this._messaging.useServiceWorker(registration);

        this._messaging.requestPermission()
          .then(result => {
            
            this._messaging.getToken()
              .then(currentToken => {
                this.currentToken.next(currentToken);
              })
              .catch(this.handleError)

          })
          .catch(error => {
            alert(error.message || error);
          })

        this._messaging.onMessage((payload: MessageData) => {
          var notification = JSON.parse(payload.data.notification) as Notification;
          window.alert("Message received >>> " + notification.title + ':' + notification.body);
        })

      })
    
  }

  subscribe (auth_token) {
    var payload = {token: this.currentToken.getValue(), platform: 'Chrome' },
        headers = new Headers({'Authorization': 'token ' + auth_token});

    return this.http.post(this.baseUrl + '/accounts/subscribe/', payload, {headers: headers})
            .toPromise()
            .then(response => {
              console.log('Token has been submitted');
            })
            .catch(this.handleError);
      
  }

  unsubscribe (auth_token) {
    var payload = {token: this.currentToken.getValue(), platform: 'Chrome' },
        headers = new Headers({'Authorization': 'token ' + auth_token});

    return this.http.post(this.baseUrl + '/accounts/unsubscribe/', payload, {headers: headers})
            .toPromise()
            .then(response => {
              console.log('Token has been removed');
            })
            .catch(this.handleError);
      
  }

  publish (payload, auth_token) {
    var headers = new Headers({'Authorization': 'token ' + auth_token});

    console.log(payload)
    return this.http.post(this.baseUrl + '/accounts/publish/', payload, {headers: headers})
            .toPromise()
            .then(response => {
              console.log('Message has been published');
            })
            .catch(this.handleError);
  }


  private handleError (error: any): Promise<any> {
    console.log('Error >>> ', error);
    var errors = error.json();
    var errMsg = Object.keys(errors).map(k => (k == 'non_field_errors' ? '' : k.toUpperCase() + ': ') + errors[k]).join('\n');
    return Promise.reject(errMsg);
  }

}



