import { Injectable } from '@angular/core';
import { Router, Resolve, RouterStateSnapshot,  ActivatedRouteSnapshot } from '@angular/router';
import { Account, DemoService } from '../demo.service';

@Injectable()
export class AccountResolver implements Resolve<Account>{

  constructor(private ds: DemoService) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot):Promise<Account> {
    return this.ds.retrieve(route.params.account_id).then(acct => {
      return acct;
    })
  }

}



