import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { SlicePipe } from '@angular/common';

import { Account, DemoService } from '../demo.service';
import { FcmService } from '../fcm.service';



export class Message {
  to: string;
  title: string;
  body: string
}

@Component({
  selector: 'app-account',
  templateUrl: './account.component.html',
  styleUrls: ['./account.component.css']
})
export class AccountComponent implements OnInit {

  private account: Account;
  private device_token: string;
  private subscribed: boolean;
  private auth_token: string;

  private message: Message;
  private recipients: Account[];

  constructor(
    private route: ActivatedRoute,
    private fs: FcmService,
    private ds: DemoService
  ) { }

  ngOnInit() {
    
    this.message = new Message();

    this.route.data.subscribe((data: {account: Account}) => {
      this.account = data.account;
    })

    this.route.params.subscribe((params: Params) => {
      this.auth_token = params['token'];
    })
    
    this.fs.currentToken.subscribe(token => {
      this.device_token = token;
      this.subscribed = this.account.devices.filter(d => d.token == this.device_token).length > 0
    })

    this.ds.list_accounts().then(accts => {
      this.recipients = accts.filter((acct:Account) => acct.notification_key && acct.uuid != this.account.uuid);
    })

  }

  subscribe() {
    
    return this.fs.subscribe(this.auth_token).then(() => this.refresh());
  }

  unsubscribe() {
    return this.fs.unsubscribe(this.auth_token).then(() => this.refresh())
  }

  refresh() {
    return this.ds.retrieve(this.account.uuid).then(acct => {
      this.account = acct;
      this.subscribed = this.account.devices.filter(d => d.token == this.device_token).length > 0
    })
    .catch(error => {
      console.log(error.message || error)
    });
  }

  publish () {
    return this.fs.publish(this.message, this.auth_token).then(res => {
      this.message = new Message();
      console.log('Message has been published...');
    })
  }
  
}



